
export default {
	globalData: {
		userInfo: null,
		headerHeight : 0,
		statusBarHeight : 0
		},
	onLaunch: function(options) {
		console.log('App Launch')
		const { model, system, statusBarHeight } = wx.getSystemInfoSync();
		var headHeight;
		if (/iphone\s{0,}x/i.test(model)) {
		headHeight = 88;
		} else if (system.indexOf('Android') !== -1) {
		headHeight = 68;
		} else {
		headHeight = 64;
		}
		this.$options.globalData.headerHeight = headHeight;
		this.$options.globalData.statusBarHeight = statusBarHeight;
	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	},
}
